import React from 'react';
import { LeagueConsumer } from './index.js';
import Key from './key.js'

const Keyboard = (props) => {
	return <LeagueConsumer>
		{(context) => (
			<>
			<Key key={1} keyName={'Hit'} keyValue={1} fontColor={'#26c7a9'} />
			<Key key={2} keyName={'Miss'} keyValue={0} fontColor={'#e13b73'} />
			<Key key={3} keyName={'Undo'} keyValue={'UNDO'} fontColor={'#5894c5'} />
			</>
		)}
	</LeagueConsumer>
};

export default Keyboard;


