import React from 'react';
import styled from 'styled-components';

const Throws = (props) => {
	const { scoreData } = props;
	
	return <ThrowsHolder>
		{renderThrows(scoreData)}
	</ThrowsHolder>;
}

const renderThrows = (scoreData) => {
	let tries = [];

	scoreData.forEach(function(item, index) {
		if (item === 0) {
			return tries.push(<MissMark key={index} />)
		}

		if (item === 1) {
			return tries.push(<HitMark key={index} />)
		}

		return tries.push(<NoMark key={index} />)
	});

	return tries;
}

const ThrowsHolder = styled.div`
	position: absolute;
	right: 0px;
	top: 3px;
	height: 50px;
	width: 10px;
`

const HitMark = styled.div`
	height: 5px;
	width: 5px;
	border-radius: 50%;
	border: solid 1px #26c7a9;
	background: #26c7a9;
	margin-bottom: 2px;
`

const MissMark = styled(HitMark)`
	background: #e13b73;
	border: solid 1px #e13b73;
`

const NoMark = styled(HitMark)`
	background: rgba(255, 255, 255, 0.1);
	border: solid 1px rgba(255, 255, 255, 0.1);
`

export default Throws;