import React, { useContext } from 'react';
import { LeagueContext } from './index.js';
import styled from 'styled-components';
import { distances } from '../../utils/distances';

const Distances = (props) => {
	const context = useContext(LeagueContext);
	const { distance } = context.state.target;
	const isReady = context.state.scoreMatrix[0];
	const { isFinal } = props;
	const width = isFinal ? '36px' : '56px';

	return <DistanceWrapper width={width}>
		{renderDistances(isReady, distance, isFinal)}
	</DistanceWrapper>
}

const renderDistances = (isReady, currentShot, isFinal) => {
	let distanceMarkers = [];

	if (!isReady) {
		return;
	}

	if (isFinal) {
		for (const distance of distances) {
			distanceMarkers.push(<FinalBox key={distance}>{`${distance}m`}</FinalBox>)
		}
	} else {
		for (const distance of distances) {
			if (distance === currentShot) {
				distanceMarkers.push(<CurrentDistance key={distance}>{`${distance}m`}</CurrentDistance>)
			} else {
				distanceMarkers.push(<Box key={distance}>{`${distance}m`}</Box>)
			}
		}
	}

	return distanceMarkers;
}

const DistanceWrapper = styled.div`
	width: ${props => props.width ? props.width : '56px'};
	padding: 4px 0px 4px 2px;
`

const Box = styled.div`
	border-radius: 2px;
	height: 50px;
	width: 50px;
	display: flex;
	flex-direction: column;
	margin-bottom: 4px;
	opacity: initial;
	background: rgba(0,0,0,.02);
	text-align: center;
	padding-top: 16px;
	box-sizing: border-box;
	transition: box-shadow .1s linear;
	color: rgba(255, 255, 255, 0.4);

	:hover {
		box-shadow: rgba(0, 0, 0, 0.16) 0px 0px 1px 0px, rgba(0, 0, 0, 0.05) 0px 2px 2px 0px, rgba(0, 0, 0, 0.06) 0px 3px 1px -2px, rgba(0, 0, 0, 0.1) 0px 1px 5px 0px;
	}

	:first-of-type {
		margin-top: 54px;
	}
`
const CurrentDistance = styled(Box)`
	background: rgba(255, 255, 255, 0.1);
`

const FinalBox = styled(Box)`
	border-radius: 0px;
	width: 36px;
	background: rgba(0, 0, 0, 0.2);

	:first-of-type {
		margin-top: 0px;
	}
`

export default Distances;