import React from 'react';
import styled from 'styled-components';

const HitBar = (props) => {
	const { width, count } = props;

	return (
		<>
			<HitBarX width={width} />
			<HitCount>{count || 0}</HitCount>
		</>
	)
}

const HitBarX = styled.div`
	height: 10px;
	width: ${props => props.width ? props.width : '0'};
	max-width: 275px;
	margin: 5px 0;
	border-top: solid 1px rgba(38,199,169,1);
	background: linear-gradient(180deg, rgba(38,199,169,0.5) 0%, rgba(38,199,169,0) 100%);
`;

const HitCount = styled.span`
	position: absolute;
	right: 7px;
	bottom: 7px;
	color: #26c7a9;
	font-size: 14px;
	padding: 1px;
`;

export default HitBar;
