import React from 'react';
import styled from 'styled-components';

const MissBar = (props) => {
	const { width, count } = props;

	return (
		<>
			<MissBarX width={width} />
			<MissCount>{count || 0}</MissCount>
		</>
	)
}

const MissBarX = styled.div`
	height: 10px;
	width: ${props => props.width ? props.width : '0'};
	max-width: 275px;
	margin: 5px 0;
	border-top: solid 1px rgba(225,59,115,1);
	background: linear-gradient(180deg, rgba(225,59,115,0.5) 0%, rgba(225,59,115,0) 100%);
`;

const MissCount = styled.span`
	position: absolute;
	right: 7px;
	top: 7px;
	color: #e13b73;
	font-size: 14px;
	padding: 1px;
`;

export default MissBar;
